import { map } from 'lodash-es'
import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import calendarCard from './fixtures/calendarCard.json';
import { CalendarType } from '../plugins/calendarCard/type';

describe('Zhi:calendarCard', () => {
  it('正确渲染', () => {
    const asl = ['root', calendarCard];
    const container = render(<Zhi content={asl} />);
    expect(container).toMatchSnapshot();
    expect(!!container.getByTestId('calendar')).toBe(true);
  });
  it('正确渲染shanhui模式', () => {
    const asl = ['root', map(calendarCard, node => node.cardType ? ({
      ...node,
      mode: CalendarType.SHANHUI
    }) : node)];
    const container = render(<Zhi content={asl} />);
    expect(container).toMatchSnapshot();
    expect(!!container.getByTestId('calendar')).toBe(true);
  });
});
